.App {
  text-align: center;
}

.container--img {
  margin-top: 50px;
  width: 350px;
}

.container--input {
  margin-top: 20px;
  width: 400px;
  height: 30px;
  padding-left: 10px;
  border-color: rgb(0, 217, 255);
  background-color: transparent;
  color: rgb(0, 217, 255);
  font-size: 16px;
}
.list--element {
  margin: 5px;
  width: 250px;
  color: white;
  list-style: none;
}
.list--element:hover {
  border: 5px solid yellowgreen;
  transform: scale(1.1);
}
.list--img {
  width: 250px;
}
.list--container {
  padding: 5px;
  background-color: rgb(0, 217, 255);
}
.list--name {
  margin: 0px;
}
.list {
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.detail--container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px;
  width: 500px;
  padding: 20px;
  color: white;
  background-color: rgb(0, 217, 255);
  text-align: center;
}
.detail--img {
  width: 250px;
}
.detail--text--container {
  padding-left: 30px;
  text-align: left;
  width: 250px;
  display: flex;
  flex-direction: column;
}
.return--button {
  color: white;
  margin-top: 20px;
  margin-bottom: 10px;
  position: relative;
  left: 240px;
}
.return--container {
  display: block;
}
.background {
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: black;
}
.detail--container--notfound {
  align-items: center;
  justify-content: center;
  padding: 0px;
  width: 500px;
  padding: 20px;
  color: white;
  background-color: rgb(0, 217, 255);
  text-align: center;
}
.not-found--text {
  color: rgb(164, 230, 148);
  margin-top: 20px;
  font-size: 20px;
}
.not-found--img {
  height: 400px;
}
a:link,
a:visited,
a:active {
  text-decoration: none;
}
